import React from 'react';
import styles from './member.module.css'
import { Form , Input , Checkbox , Button} from 'antd';

const Member = () => {
    return (
        <div className={styles.member}>
            <Form >
                <div>
                    <label htmlFor="user-id"></label><br/>
                    <Input name="user-id"  />
                </div>
                <div>
                    <label htmlFor="user-nick"></label><br/>
                    <Input name="user-nick"  />
                </div>
                <div>
                    <label htmlFor="user-password"></label><br/>
                    <Input name="user-password" type="password"  />
                </div>
                <div>
                    <label htmlFor="user-password-check"></label><br/>
                    <Input name="user-password-check" type="password"  />
                </div>
                <div>
                    <Checkbox name="user-term" > ?</Checkbox>
                </div>
                <div style={{marginTop:10}}>
                    <Button type="primary" htmlType="submit" ></Button>
                </div>
            </Form>
        </div>
    );
};

export default Member;